import request from "../utils/request";

export function login(username, password) {

  return request({
    url: "/login",
    method: "post",
    data: {
      username, password
    }
  })

}


export function logout() {

  return request({
    url: "/logout",
    method: "get",
  })

}


export function getOperatorInfo(uid) {
  return request({
    url: "/getUserInfo",
    method: "get",
    params: {
      uid
    }
  })

}


export function getOperatorList(currentPage, pageSize, sortBy = "", sort = "", status = '', searchBy = "", searchValue = "", startTime = "", endTime = "") {
  return request({
    url: "/Operator",
    method: "get",
    params: {
      searchBy,
      searchValue,
      startTime,
      endTime,
      sortBy,
      sort,
      status,
      currentPage,
      pageSize,
    }
  })

}



