import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/User/login'
import Register from '@/components/User/register'
import Frame from '@/components/frame'
import Home from '@/components/home/home'


import Layout from "../components/_Layout/Layout";


// 流水报表
const WechatEnterprisePay = () => import('@/components/Statement/WechatEnterprisePay');
const WechatRedPackage = () => import('@/components/Statement/WechatRedPackage');
const DailyStatistical = () => import('@/components/Statement/DailyStatistical');
const DailyStatisticalWrap = () => import('@/components/Statement/DailyStatisticalWrap');
const ChargeStatistics = () => import('@/components/Statement/ChargeStatistics');
// const Task = () => import('@/components/Statement/Task');

const Task = function () {

  return import('@/components/Statement/Task');
};

// 渠道
const ChannelManage = () => import('@/components/Channel/ChannelManage');


//用户中心

const PlayerList = () => import('@/components/Player/PlayerList');
const PlayerInfo = () => import('@/components/Player/PlayerInfo');


//微信相关
const WechatJump = () => import('@/components/Wechat/WechatJump')


Vue.use(Router);


const r = new Router({
  routes: [
    {
      path: '/Login',
      name: 'Login',
      component: Login,
      meta: {
        title: '登录'
      }
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
      meta: {
        title: '注册'
      }
    },
    {
      path: '/',
      name: 'Frame',
      component: Frame,
      // redirect: '/Login',
      children: [
        {path: '/', redirect: 'home'},
        {
          path: '/home',
          name: 'Home',
          component: Home,
          meta: {
            title: '首页'
          }
        },
        {
          path: 'Player',
          name: 'Player',
          redirect: 'Player/PlayerList',
          component: Layout,
          meta: {
            title: '用户管理'
          },
          children: [
            {
              path: 'PlayerList',
              name: 'PlayerList',
              component: PlayerList,
              meta: {
                title: '用户列表'
              }
            },
            {
              path: 'PlayerInfo',
              name: 'PlayerInfo',
              component: PlayerInfo,
              hidden: true,
              meta: {
                title: '用户信息'
              }
            },
          ]
        },
        {
          path: 'Statement',
          name: 'Statement',
          redirect: 'Statement/WechatRedPackage',
          component: Layout,
          meta: {
            title: '流水报表'
          },
          children: [
            {
              path: 'WechatRedPackage',
              name: 'WechatRedPackage',
              component: WechatRedPackage,
              meta: {
                title: '微信红包'
              }
            },
            {
              path: 'DailyStatisticalWrap',
              name: 'DailyStatisticalWrap',
              component: DailyStatisticalWrap,
              meta: {
                title: '每日统计'
              }
            },
            {
              path: 'ChargeStatistics',
              name: 'ChargeStatistics',
              component: ChargeStatistics,
              meta: {
                title: '充值统计'
              }
            },
            {
              path: 'WechatEnterprisePay',
              name: 'WechatEnterprisePay',
              component: WechatEnterprisePay,
              meta: {
                title: '微信企业付款'
              }
            },
            {
              path: 'Task',
              name: 'Task',
              component: Task,
              meta: {
                title: '任务汇总'
              }
            }

          ]

        },
        {
          path: 'Channel',
          name: 'Channel',
          redirect: 'Channel/ChannelManage',
          component: Layout,
          meta: {
            title: '渠道管理'
          },
          children: [
            {
              path: 'ChannelManage',
              name: 'ChannelManage',
              component: ChannelManage,
              meta: {
                title: '渠道管理'
              }
            },


          ]
        },
        {
          path: 'WechatModule',
          name: 'WechatModule',
          redirect: 'WechatModule/WechatJump',
          component: Layout,
          meta: {
            title: '微信'
          },
          children: [
            {
              path: 'WechatJump',
              name: 'WechatJump',
              component: WechatJump,
              meta: {
                title: '微信跳出'
              }
            },


          ]
        }, {
          path: 'Operator',
          name: 'Operator',
          redirect: 'Operator/Operator',
          component: Layout,
          meta: {
            title: '后台用户'
          },
          children: [
            {
              path: 'OperatorList',
              name: 'OperatorList',
              component: () => import('@/components/Operator/OperatorList'),
              meta: {
                title: '后台用户列表'
              }
            },


          ]
        }
      ]
    }

  ]
})


